import React, { useState, useEffect } from "react";
import {
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter,
} from "@salesforce/design-system-react";
import { ModalContext } from "../ModalContext";
import { useQuery, gql } from "@apollo/client";
import OpportunityForm from "../opportunities/ModalAddOpportunity";
import { useSelector } from "react-redux";

function OpportunitiesList(props) {
  const opportunities = useSelector((state) => state.opportunities);

  console.log(opportunities);

  const { handleModal } = React.useContext(ModalContext);

  const SINGLE = gql`
	  query GetLocations {
	    getOpportunitiesByCompany(company_id: ${props.company}) {
	      id
        amount
        stage
	    }
	  }
	`;
  const { loading, error, data } = useQuery(SINGLE);
  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  const triggerModal = (content, heading) => {
    handleModal(
      <OpportunityForm addOpportunity={props.addOpportunity} />,
      "Add Opportunity"
    );
  };

  return (
    <IconSettings iconPath="/icons">
      <Card
        id="OpportunityCard"
        headerActions={
          <Button label="Add Opportunity" onClick={() => triggerModal()} />
        }
        filter={
          data.getOpportunitiesByCompany.length > 0 && (
            <CardFilter
              onChange={() => {
                console.log("Change");
              }}
            />
          )
        }
        heading={`Opportunities (${data.getOpportunitiesByCompany.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.getOpportunitiesByCompany.length === 0 ? (
            <CardEmpty heading="No Opportunities">
              <Button label="Add Opportunity" onClick={() => triggerModal()} />
            </CardEmpty>
          ) : null
        }
      >
        <ul>
          {data.getOpportunitiesByCompany.map((opportunity) => {
            return (
              <li>
                {opportunity.name} {opportunity.amount} {opportunity.stage}
              </li>
            );
          })}
        </ul>
      </Card>
    </IconSettings>
  );
}

export default OpportunitiesList;
