import React, {useState} from 'react';
import {
  DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  DataTableRowActions,
  Dropdown,
  IconSettings,
  CardFilter,
} from "@salesforce/design-system-react";
import {AddProposal} from './ModalAddProposal';
import { ModalContext } from "../ModalContext";

export function ProposalTab(props){
	  const { handleModal } = React.useContext(ModalContext);

	const [proposals, setProposals] = useState(props.proposals);

	const triggerModal = (content,heading) => {
	    handleModal(<AddProposal />,'Add Proposal')
	  }
	return (
		<div>
			<Card
		        id="ProposalCard"
		        headerActions={
		          <Button label="Add Proposals" onClick={() => triggerModal()}/>
		        }
		        filter={
		          proposals.length > 0 && (
		            <CardFilter
		              onChange={() => {
		                console.log("Change");
		              }}
		            />
		          )
		        }
		        heading={`Proposal Versions (${proposals.length})`}
		        icon={<Icon category="standard" name="contact" size="small" />}
		        empty={
		          proposals.length === 0 ? (
		            <CardEmpty heading="No Proposals">
		              <Button
		                label="Add Proposals"
		              />
		            </CardEmpty>
		          ) : null
		        }
		      >
		        <DataTable
		          fixedLayout
		          items={proposals}
		          id="proposals"
		        >
		          <DataTableColumn label="Id" property="id"></DataTableColumn>

		          <DataTableColumn label="Created at">
		          </DataTableColumn>

		          <DataTableRowActions
					options={[
						{
							id: 0,
							label: 'Edit',
							value: '1',
						},
						{
							id: 1,
							label: 'Send to contacts',
							value: '2',
						},
						{
							id: 2,
							label: 'Remove',
							value: '3',
						}				
					]}
					dropdown={<Dropdown length="7" />}
					/>
		        </DataTable>
		      </Card>
		</div>
	)
}