import React, { useState, useEffect } from "react";
import {
  DataTable,
  DataTableColumn,
  DataTableCell,
  BrandBand,
  PageHeaderControl,
  PageHeader,
  Input,
  Button,
  DropdownTrigger,
  ButtonGroup,
  Dropdown,
  Icon,
  IconSettings,
  Modal,
} from "@salesforce/design-system-react";
import { useQuery, gql } from "@apollo/client";
import { CompanyForm } from "./ModalAddCompanyForm";
import {
  Link,
  useHistory,
  BrowserRouter as Router,
  Route,
  useLocation,
} from "react-router-dom";
import { ModalContext } from "../ModalContext";
import { useSelector } from "react-redux";

const COMPANIES = gql`
  query GetCompanies {
    companies {
      id
      company_name
      city
      state
      category
      contacts {
        id
      }
      locations {
        id
      }
      created_at
    }
  }
`;

const CompanyLink = ({ children, item, ...props }) => (
  <DataTableCell title={children} {...props}>
    <Link to={`/companies/${item.id}`}>{children}</Link>
  </DataTableCell>
);
CompanyLink.displayName = DataTableCell.displayName;

const LocationCell = ({ children, item, ...props }) => (
  <DataTableCell {...props}>
    <a href="javascript:void(0);">{item.locations.length}</a>
  </DataTableCell>
);
LocationCell.displayName = DataTableCell.displayName;

export function CompaniesList(props) {
  const user = useSelector((state) => state.user);

  console.log(user);

  const { handleModal } = React.useContext(ModalContext);
  const { loading, error, data } = useQuery(COMPANIES);
  const [items, setItems] = useState([]);

  useEffect(() => {
    if (data) {
      console.log("triggered", data);
      setItems(data.companies);
    }
  }, [loading]);
  const triggerModal = (content, heading) => {
    handleModal(<CompanyForm auth={user} />, "Add Company");
  };

  const filterTable = (input) => {
    setItems(
      data.companies.filter((item) =>
        item.company_name.toLowerCase().includes(input.toLowerCase())
      )
    );
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  console.log(data);

  const actions = () => (
    <PageHeaderControl>
      <ButtonGroup variant="list" id="button-group-page-header-actions">
        <ButtonGroup variant="list" id="button-group-page-header-actions">
          <Button
            variant="brand"
            label="Add Company"
            onClick={() => triggerModal()}
          />
        </ButtonGroup>
      </ButtonGroup>
    </PageHeaderControl>
  );

  const controls = () => (
    <React.Fragment>
      <PageHeaderControl>
        <Input
          placeholder="Search companies"
          type="text"
          onInput={(e) => filterTable(e.target.value)}
        />
      </PageHeaderControl>
      <PageHeaderControl>
        <Dropdown
          align="right"
          id="page-header-dropdown-object-home-content-right"
          options={[
            { label: "Menu Item One", value: "A0" },
            { label: "Menu Item Two", value: "B0" },
            { label: "Menu Item Three", value: "C0" },
            { type: "divider" },
            { label: "Menu Item Four", value: "D0" },
          ]}
        >
          <DropdownTrigger>
            <Button
              assistiveText={{ icon: "List View Controls" }}
              iconCategory="utility"
              iconName="settings"
              iconVariant="more"
            />
          </DropdownTrigger>
        </Dropdown>
      </PageHeaderControl>
    </React.Fragment>
  );

  return (
    <IconSettings iconPath="/icons">
      <BrandBand
        id="brand-band-lightning-blue"
        className="slds-p-around_small"
        theme="lightning-blue"
      >
        <PageHeader
          icon={
            <Icon
              assistiveText={{ label: "User" }}
              category="standard"
              name="opportunity"
            />
          }
          label="COMPANY"
          title={`Your companies`}
          variant="object-home"
          info={`${items.length} companies  Updated 13 minutes ago`}
          onRenderActions={actions}
          onRenderControls={controls}
        />
        <DataTable fixedLayout items={items} id="getCompanies" on>
          <DataTableColumn label="Id" property="id"></DataTableColumn>
          <DataTableColumn label="Company name" property="company_name">
            <CompanyLink />
          </DataTableColumn>
          <DataTableColumn label="Website" property="website"></DataTableColumn>
          <DataTableColumn
            label="Created at"
            property="created_at"
          ></DataTableColumn>
          <DataTableColumn
            label="Phone number"
            property="phone_number"
          ></DataTableColumn>
          <DataTableColumn label="Locations">
            <LocationCell locations={items.locations} />
          </DataTableColumn>

          <DataTableColumn label="Opportunities"></DataTableColumn>

          <DataTableColumn label="Value"></DataTableColumn>

          <DataTableColumn label="Projects">
            <LocationCell />
          </DataTableColumn>
        </DataTable>
      </BrandBand>
    </IconSettings>
  );
}
