import React, {useState,useEffect} from 'react';
import {
  Input,Modal,Button,
  IconSettings,Icon,
  DataTable,DataTableColumn,DataTableCell,DataTableRowActions} 
from '@salesforce/design-system-react'; 

import { useQuery, gql } from '@apollo/client';

const CONTACTS = gql`
  query GetContacts {
    getContacts {
      id
      first_name
      last_name
      email
      phone_number
    }
  }
`;

const CustomDataTableCell = ({ children, ...props }) => (
  <DataTableCell title={children} {...props}>
    <a
      href="javascript:void(0);"
      onClick={(event) => {
        event.preventDefault();
      }}
    >
      {children}
    </a>
  </DataTableCell>
);
CustomDataTableCell.displayName = DataTableCell.displayName;

export function AddContactToLocation(props) {
  const [modalOpen, setModalOpen] = useState(true)
  const [selectedContacts, setSelectedContacts] = useState([]);

  const { loading, error, data } = useQuery(CONTACTS);

  useEffect(() => {
    setModalOpen(props.toggle)
  },[props]);

  function toggleModalOpen(){
    setModalOpen(!modalOpen)
  };

  function handleChanged(event, data) {
    setSelectedContacts(data.selection);
  };
  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  return (
  	<Modal
      ariaHideApp={false}
      isOpen={modalOpen}
      footer={[
        <Button label="Cancel" onClick={toggleModalOpen} />,
        <Button label="Next" variant="brand" onClick={toggleModalOpen} />,
      ]}
      onRequestClose={toggleModalOpen}
      heading="Add Contacts to Location"
    >
    	<section className="slds-p-around_large">
        
        <DataTable
            assistiveText={{
              actionsHeader: 'actions',
              columnSort: 'sort this column',
              columnSortedAscending: 'asc',
              columnSortedDescending: 'desc',
              selectAllRows: 'Select all rows',
              selectRow: 'Select this row',
            }}
            fixedLayout
            items={data.getContacts}
            id="DataTableExample-2"
            selectRows="checkbox"
            onRowChange={handleChanged}
            selection={selectedContacts}
          >
            <DataTableColumn
              label="Id"
              property="id"
            >

            </DataTableColumn>
            <DataTableColumn
              label="First Name"
              property="first_name"
            >

            </DataTableColumn>
            <DataTableColumn
              label="Last Name"
              property="last_name"
            >

            </DataTableColumn>
            <DataTableColumn
              label="Email"
              property="email"
            >

            </DataTableColumn>
            <DataTableColumn
              label="Phone"
              property="phone_number"
            >

            </DataTableColumn>
        </DataTable>


    	</section>
    </Modal>
  )
}