import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter
 } 
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';
import { Link,useHistory,BrowserRouter as Router, Route, useLocation } from "react-router-dom";

const RecordLink = ({children, item, ...props}) => (
  <DataTableCell title={children} {...props}>
    <Link to={`/contacts/${item.id}`}>
      {children}
    </Link>
  </DataTableCell>
)
RecordLink.displayName = DataTableCell.displayName;

export function ContactsList(props) {
	const SINGLE = gql`
	  query GetContacts($id: ID!) {
	    company(id: $id) {
	      contacts {
          id
	        contact_name
	       company_title
        }
	    }
	  }
	`;
	const { loading, error, data } = useQuery(SINGLE, {
    variables: { id: parseInt(props.company) },
  });
	if (loading) return <p>Loading...</p>;
	if (error) return <p>Error :(</p>;
	return (
		<IconSettings iconPath="/icons">
      <Card
        id="ContactCard"
        headerActions={
         (
            <Button
              label="Add Contacts"
              onClick={console.log('Open modal')}
            />
          )
        }
        filter={
         (data.company.contacts.length > 0) && (
            <CardFilter onChange={() => {console.log('Change')}} />
          )
        }
        heading={`Contacts (${data.company.contacts.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.company.contacts.length === 0 ? (
            <CardEmpty heading="No Contacts">
              <Button label="Add Contact" onClick={() => console.log('Clicked')} />
            </CardEmpty>
          ) : null
        }
      >
  			<DataTable
          fixedLayout
          items={data.company.contacts}
          id="getContacts"
          on
        >
          <DataTableColumn
            label="Id"
            property="id"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Contact name"
            property="contact_name"
          >
            <RecordLink />

          </DataTableColumn>
          <DataTableColumn
            label="Company title"
            property="company_title"
          >

          </DataTableColumn>
         

        </DataTable>
      </Card>
		</IconSettings>
	)
}