import React, { useState } from "react";
import { useForm, Controller } from "react-hook-form";
import moment from "moment";

import { Input, Datepicker } from "@salesforce/design-system-react";

import { useDispatch } from "react-redux";
import { addOpportunity } from "../../redux/opportunity/opportunityActions";

function OpportunityForm() {
  const [startDateValue, setStartDateValue] = useState("");
  const [closeDateValue, setCloseDateValue] = useState("");

  const dispatch = useDispatch();

  const { register, handleSubmit, reset, watch, control } = useForm();

  const onSubmit = (data) => {
    console.log(data);
    dispatch(addOpportunity(data));
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Controller
        as={
          <label htmlFor="opportunityName">
            Name
            <Input name="opportunity_name" type="text" ref={register} />
          </label>
        }
        control={control}
        name="opportunity_name"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="opportunityDuration">
            Duration
            <Input name="duration" type="number" ref={register} />
          </label>
        }
        control={control}
        name="duration"
        defaultValue={""}
      />

      <Controller
        as={
          <Datepicker
            labels={{
              label: "Start Date",
            }}
            onChange={(event, data) => {
              this.handleChange(event, data);

              if (this.props.action) {
                const dataAsArray = Object.keys(data).map((key) => data[key]);
                this.props.action("onChange")(event, data, ...dataAsArray);
              } else if (console) {
                console.log("onChange", event, data);
              }
            }}
            onCalendarFocus={(event, data) => {
              if (this.props.action) {
                const dataAsArray = Object.keys(data).map((key) => data[key]);
                this.props.action("onCalendarFocus")(
                  event,
                  data,
                  ...dataAsArray
                );
              } else if (console) {
                console.log("onCalendarFocus", event, data);
              }
            }}
            formatter={(date) => {
              return date ? moment(date).format("M/D/YYYY") : "";
            }}
            parser={(dateString) => {
              return moment(dateString, "MM-DD-YYYY").toDate();
            }}
            value={startDateValue}
          />
        }
        control={control}
        name="duration"
        defaultValue={""}
      />

      <Controller
        as={
          <Datepicker
            labels={{
              label: "Close Date",
            }}
            onChange={(event, data) => {
              this.handleChange(event, data);

              if (this.props.action) {
                const dataAsArray = Object.keys(data).map((key) => data[key]);
                this.props.action("onChange")(event, data, ...dataAsArray);
              } else if (console) {
                console.log("onChange", event, data);
              }
            }}
            onCalendarFocus={(event, data) => {
              if (this.props.action) {
                const dataAsArray = Object.keys(data).map((key) => data[key]);
                this.props.action("onCalendarFocus")(
                  event,
                  data,
                  ...dataAsArray
                );
              } else if (console) {
                console.log("onCalendarFocus", event, data);
              }
            }}
            formatter={(date) => {
              return date ? moment(date).format("M/D/YYYY") : "";
            }}
            parser={(dateString) => {
              return moment(dateString, "MM-DD-YYYY").toDate();
            }}
            value={closeDateValue}
          />
        }
        control={control}
        name="duration"
        defaultValue={""}
      />
      <br />
      <Controller
        as={
          <label htmlFor="totalAmount">
            Amount
            <Input name="total_amount" type="number" ref={register} />
          </label>
        }
        control={control}
        name="total_amount"
        defaultValue={""}
      />

      <Input type="submit" />
    </form>
  );
}

export default OpportunityForm;
