import React, {useState} from 'react';
import Moment from "react-moment";

import {
	BreadCrumb,
	PageHeader,
	Icon,
	BrandBand,
	Tabs,
	TabsPanel,
	PageHeaderControl,
	ButtonGroup,
	Button,
	Dropdown,
	ExpandableSection
} from '@salesforce/design-system-react';
import {Link} from 'react-router-dom'
import Board from '@lourenci/react-kanban'
import '@lourenci/react-kanban/dist/styles.css'
import { useQuery, gql } from '@apollo/client';

const OPPORTUNITIES = gql`
	{
	  projects {
	    id
	    project_name
	    start_date
	    total_amount
	    stage
	    is_opportunity
		created_by {
			user_name
		}	   
	  }
	}
`;


export function OpportunitiesList({match}) {

  const { loading, error, data } = useQuery(OPPORTUNITIES);	
  const [opportunities, setOpportunities] = [];
	  const actions = () => (
		<PageHeaderControl>
			<ButtonGroup variant="list" id="button-group-page-header-actions">
				<ButtonGroup variant="list" id="button-group-page-header-actions">
					<Button variant="brand" label="Add Opportunity" onClick={toggleModal}/>
				</ButtonGroup>
			</ButtonGroup>
		</PageHeaderControl>
	);

  const trail = [
		<a href="javascript:void(0);">Accounts</a>,
		<a href="javascript:void(0);">Company One</a>,
	];

	function toggleModal() {
		console.log(data);
	}

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  let arr = [];

  data.projects.map(opportunity => {
  	if (opportunity.is_opportunity) {
  		arr.push({id: opportunity.id, title: opportunity.project_name, description: opportunity.company_id, stage: opportunity.stage, created_by: opportunity.created_by.user_name, start_date: opportunity.start_date})
  	}
  })

  const board = {
	  columns: [
	    {
	      id: 1,
	      title: 'Contacted',
	      cards: arr
	    },
      {
        id: 2,
        title: 'Open',
        cards: []
      },
      {
        id: 3,
        title: 'Qualified',
        cards: []
      },
      {
        id: 4,
        title: 'Nurturing',
        cards: []
      },
      {
        id: 5,
        title: 'Closed',
        cards: []
      }
	  ]
	}
	return (
		<BrandBand
				id="brand-band-lightning-blue"
				className="slds-p-around_small"
				theme="lightning-blue"
			>
			<PageHeader
				icon={
					<Icon
						assistiveText={{ label: 'User' }}
						category="standard"
						name="opportunity"
					/>
				}
				info={`${data.projects.length} items`}
				label="OPPORTUNITIES"
				title={`Your opportunities`}
				variant="object-home"
				truncate
				onRenderActions={actions}
			/>
			<div className="slds-box slds-stages slds-p-vertical_none ">
				<div className="slds-grid slds-p-horizontal_none slds-gutters">
					<Board  
          allowAddColumn={false}
          disableColumnDrag={true}
					renderColumnHeader={({ title }, { removeColumn, renameColumn, addCard }) => (
					    <div className="stage-header">
					      <h2>{title}</h2>
					    </div>
					  )}
					renderCard={({ id,title,description, created_by,start_date }, { removeCard, dragging }) => (
				    <Card id={id} created_by={created_by} start_date={start_date} title={title} company={description} dragging={dragging}>
				      <button type="button" onClick={removeCard}>Remove Card</button>
				    </Card>
				  )}
					initialBoard={board} />
				</div>
			</div>
		</BrandBand>
	)
}

function Stage(props) {
	const title = props.title;
	return (
		<div className="slds-col slds-p-horizontal_none stage">
			<h2 className="stage__title">{title}</h2>
			<h4 className="stage__total">$100</h4>
			<div className="slds-box">
				<Card title="Hey" company="Whoa" stage="Qualified" date_closed="10/17/2020" />
			</div>
		</div>
	)
}

function Card(props) {
	const title = props.title;
	const company = props.company;
	const stage = props.stage;
	const created_by = props.created_by;
	const date_closed = props.date_closed;
	const start_date = props.start_date;
	return (
		<div className="slds-box card">
			<Link to={`/companies/${props.company}/opportunities/${props.id}`}>{title}</Link>
			<p>{company}</p>
			<p>Submitted by: {created_by}</p>
			<p>Expected start date: <Moment date={start_date} format="MM/DD/YYYY" /></p>
		</div>
	)
}