import React from "react";
import "../index.css";
// import App from "../App";
import "../App.css";

import { ApolloClient, ApolloProvider, InMemoryCache } from "@apollo/client";
import { ModalProvider } from "./ModalContext";

// import store from '../app/store';
import store from "../redux/store";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Provider } from "react-redux";
import { useHistory } from "react-router-dom";

import { CompanyForm } from "../components/companies/ModalAddCompanyForm";

import {
  PageHeader,
  GlobalHeader,
  GlobalNavigationBar,
  IconSettings,
  GlobalNavigationBarRegion,
  GlobalNavigationBarLink,
  GlobalHeaderNotifications,
  AppLauncher,
  AppLauncherExpandableSection,
  AppLauncherTile,
  Button,
  Icon,
  Popover,
} from "@salesforce/design-system-react";
import { Header } from "./Header";
import { NavBar } from "./Navbar";

// View imports
import { CompanyDetail } from "../components/companies/CompanyDetail";
import LocationDetail from "../components/locations/LocationDetail";
import { CompaniesList } from "../components/companies/CompaniesList";
import { ContactsList } from "../components/contacts/ContactsList";
import { ContactDetail } from "../components/contacts/ContactDetail";
import { OpportunitiesList } from "../components/opportunities/OpportunitiesList";
import { OpportunityDetail } from "../components/opportunities/OpportunityDetail";

const client = new ApolloClient({
  uri: "http://127.0.0.1:4000/graphql",
  // uri: "http://bba-server.herokuapp.com/graphql",
  cache: new InMemoryCache(),
  onError: ({ networkError, graphQLErrors }) => {
    console.log("graphQLErrors", graphQLErrors);
    console.log("networkError", networkError);
  },
});

const Root = () => (
  <ApolloProvider client={client}>
    <Provider store={store}>
      <ModalProvider>
        <Router>
          <IconSettings iconPath="/icons">
            <div className="App">
              <Header />
              <NavBar />

              <Route exact path="/" component={CompaniesList} />

              <Route exact path="/companies" component={CompaniesList} />
              <Route
                exact
                path="/companies/:companyId"
                component={CompanyDetail}
              />
              <Route
                exact
                path="/companies/:companyId/locations/:locationId"
                component={LocationDetail}
              />

              <Route exact path="/contacts" component={ContactsList} />
              <Route
                exact
                path="/contacts/:contactId"
                component={ContactDetail}
              />

              <Route
                exact
                path="/opportunities"
                component={OpportunitiesList}
              />

              <Route
                exact
                path="/companies/:companyId/opportunities/:opportunityId"
                component={OpportunityDetail}
              />
            </div>
          </IconSettings>
        </Router>
      </ModalProvider>
    </Provider>
  </ApolloProvider>
);

export default Root;
