import React, { useState, useEffect } from "react";
import {
  DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter,
} from "@salesforce/design-system-react";
import { useQuery, gql } from "@apollo/client";
import {
  Link,
  useHistory,
  BrowserRouter as Router,
  Route,
  useLocation,
} from "react-router-dom";
import { useSelector } from "react-redux";
import { ModalContext } from "../ModalContext";
import { AddLocation } from "../locations/ModalAddLocation";

const LocationLink = ({ children, item, ...props }) => (
  <DataTableCell title={children} {...props}>
    <Link to={`/companies/${props.company}/locations/${item.id}`}>
      {children}
    </Link>
  </DataTableCell>
);
LocationLink.displayName = DataTableCell.displayName;

export function LocationsList(props) {
  const locations = useSelector((state) => state.locations);

  console.log(locations);

  const { handleModal } = React.useContext(ModalContext);

  const triggerModal = (content, heading) => {
    handleModal(<AddLocation />, "Add Location");
  };

  const SINGLE = gql`
    query GetOpportunities($id: ID!) {
      company(id: $id) {
        locations {
          id
          location_name
          city
          state
          location_code
        }
      }
    }
  `;
  const { loading, error, data } = useQuery(SINGLE, {
    variables: { id: parseInt(props.company) },
  });
  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
  return (
    <IconSettings iconPath="/icons">
      <Card
        id="ContactCard"
        headerActions={
          <Button label="Add Locations" onClick={() => triggerModal()} />
        }
        filter={
          data.company.locations.length > 0 && (
            <CardFilter
              onChange={() => {
                console.log("Change");
              }}
            />
          )
        }
        heading={`Locations (${data.company.locations.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.company.locations.length === 0 ? (
            <CardEmpty heading="No Locations">
              <Button
                label="Add Location"
                // onClick={() => console.log("Clicked")}
                onClick={() => triggerModal()}
              />
            </CardEmpty>
          ) : null
        }
      >
        <DataTable
          fixedLayout
          items={data.company.locations}
          id="getContacts"
          on
        >
          <DataTableColumn label="Id" property="id"></DataTableColumn>
          <DataTableColumn label="Location Name" property="location_name">
            <LocationLink company={props.company} />
          </DataTableColumn>
          <DataTableColumn label="City" property="city">

          </DataTableColumn>

          <DataTableColumn label="State" property="state"></DataTableColumn>

          <DataTableColumn
            label="Created at"
            property="created_at"
          ></DataTableColumn>
        </DataTable>
      </Card>
    </IconSettings>
  );
}
