import React, {useState,useEffect} from 'react';
import {
  GlobalHeader,
  GlobalNavigationBar,
  GlobalNavigationBarRegion,
  GlobalNavigationBarLink,
  GlobalHeaderNotifications,
  AppLauncher,
  AppLauncherExpandableSection,
  AppLauncherTile,
  Button,
  Icon} from '@salesforce/design-system-react';

  import { useHistory,BrowserRouter as Router, Route, useLocation } from "react-router-dom";

export function NavBar() {
	const history = useHistory();
	const [currentLocation, setCurrentLocation] = useState("");
	let location = useLocation();

	useEffect( () => {
		setCurrentLocation(location.pathname);
	},[location])

	function handleNav(path) {
		history.push(path);
	}
	return (
		<GlobalNavigationBar>
		<GlobalNavigationBarRegion region="primary">
			<AppLauncher
				id="app-launcher-trigger"
				triggerName="Sales"
				onSearch={(e) => {
					console.log('Search term:', e.target.value);
				}}
				modalHeaderButton={<Button label="Get Help" />}
			>
				<AppLauncherExpandableSection title="Project Management">
					<AppLauncherTile
						title="Current Projects"
						iconText="MC"
						description="Check current progress status reports"
					/>
					<AppLauncherTile
						title="Upcoming Projects"
						description="View the incoming projects from sales."
						iconText="UP"
					/>
				</AppLauncherExpandableSection>
				<AppLauncherExpandableSection title="Sales">
					<AppLauncherTile
						title="CRM"
						iconText="JB"
						size="small"
					/>
					<AppLauncherTile
						title="Opportunity Pipeline"
						iconNode={
							<Icon name="campaign" category="standard" size="large" />
						}
						size="small"
					/>
					<AppLauncherTile
						title="Sales Reports"
						iconNode={
							<Icon name="campaign" category="standard" size="large" />
						}
						size="small"
					/>
				</AppLauncherExpandableSection>
				<AppLauncherExpandableSection title="Technician Communication">
					<AppLauncherTile
						title="Issue Tracking"
						iconText="JB"
						size="small"
					/>
					<AppLauncherTile
						title="Availability"
						iconNode={
							<Icon name="campaign" category="standard" size="large" />
						}
						size="small"
					/>
				</AppLauncherExpandableSection>
			</AppLauncher>
		</GlobalNavigationBarRegion>
        <GlobalNavigationBarRegion region="secondary" navigation>
          <GlobalNavigationBarLink active={currentLocation.includes('companies')} onClick={(path) => handleNav('/companies')} label="Companies" id="home-link" />
          <GlobalNavigationBarLink active={currentLocation.includes('contacts')} onClick={(path) => handleNav('/contacts')} label="Contacts" />
          <GlobalNavigationBarLink active={currentLocation.includes('opportunities')} onClick={(path) => handleNav('/opportunities')} label="Opportunities" />
          <GlobalNavigationBarLink active={currentLocation.includes('projects')} onClick={(path) => handleNav('/reports')} label="Projects" />
          <GlobalNavigationBarLink active={currentLocation.includes('reportss')} onClick={(path) => handleNav('/reports')} label="Reports" />
        </GlobalNavigationBarRegion>
      </GlobalNavigationBar>
	)
}
