import React, {useState,useEffect} from 'react';
import { useQuery, gql } from '@apollo/client';
import { useForm } from 'react-hook-form';
import {
  PageHeader,
  PageHeaderControl,
  IconSettings,
  Icon,
  Tabs,
  TabsPanel,
  ButtonStateful,
  Button,
  ButtonGroup,
  Dropdown,
  BrandBand,
  Input,
  InputIcon
} 
from '@salesforce/design-system-react'; 
import { Link,useHistory,BrowserRouter as Router, Route, useLocation } from "react-router-dom";
import { FlatfileButton } from "@flatfile/react";

import {ContactsList} from './ContactsList';
import {OpportunitiesList} from './OpportunitiesList';
import {EquipmentList} from './EquipmentList';
import {ProjectsList} from './ProjectsList';

import { addEquipment } from "../../redux/equipment/equipmentActions";
import { connect } from "react-redux";

function LocationDetail({match,props}) {
  const SINGLE = gql`
    query locationById($location_id: ID!,$company_id: ID!) {
      location(id: $location_id) {
        id
        city
        state
        address
        location_name
      }
      company(id: $company_id) {
        id
        company_name
      }
    }
  `;
  const { loading, error, data } = useQuery(SINGLE, {
    variables: { location_id: parseInt(match.params.locationId),company_id: parseInt(match.params.companyId) },
  });

  const [modalOpen,toggleModalOpen] = useState(false);

  const [equipment, setEquipment] = useState([]);

  const { register, handleSubmit, errors } = useForm();
  const onSubmit = data => console.log(data);

  const actions = () => (
      <React.Fragment>
        <PageHeaderControl>
          <ButtonGroup variant="list" id="button-group-page-header-actions">
            
            <FlatfileButton
                  licenseKey="e335c423-3909-4363-accf-88c0cd05947f"
                  customer={{ userId: "12345" }}
                  settings={{
                    type: "Equipment",
                    fields: [
                      { label: "Building Code", key: "building_code" },
                      { label: "System name", key: "system_name" },
                      { label: "Building", key: "building" },
                      { label: "CFM", key: "cfm" },
                      { label: "Size", key: "size" },
                      { label: "Water", key: "water", type: 'checkbox'},
                      { label: "Power", key: "power", type: 'checkbox' }
                    ]
                  }}
                  onData={async (results) => {
                    // do something with the results
                    let arr = [];

                    results.data.map(obj => {

                      let cfm = obj.cfm.replace(/,/g, '');

                      let newObj = {
                        building_code: obj.building_code,
                        system_name: obj.system_name,
                        cfm: parseInt(cfm),
                        size: obj.size,
                        water: obj.water,
                        power: obj.power,
                        location_id: parseInt(match.params.locationId)
                      };
                      arr.push(newObj);
                    })
                    addEquipment(arr);
                    setEquipment(arr);
                    return "Done!";
                  }}
                  render={(importer, launch) => {
                    return <Button onClick={launch} label="Upload Equipment List" />;
                  }}
              >
              </FlatfileButton>
            <Button label="New Opportunity" />
          </ButtonGroup>
        </PageHeaderControl>
      </React.Fragment>
    );

  

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
    const details = [
      {
        label: 'Address',
        content: <p>{data.location.address}<br/>{data.location.city}, {data.location.state} 75201</p>
      },
      { label: 'Company', content: <Link to={`/companies/${data.company.id}`}>{data.company.company_name}</Link>},
      {
        label: 'Primary contact',
        content: <a href="">Jon Smith</a>,
        truncate: true,
      },
      {
        label: 'Created at',
        content: new Date().getTime(),
        truncate: true,
      },
    ];

  return (

    <IconSettings iconPath="/icons">
      <BrandBand
          id="brand-band-lightning-blue"
          className="slds-p-around_small"
          theme="lightning-blue"
        >
        <PageHeader
          details={details}
          icon={
            <Icon
              assistiveText={{ label: 'Location' }}
              category="standard"
              name="account"
            />
          }
          label={`Location ID ${data.location.id}`}
          onRenderActions={actions}
          title={data.location.name}
          variant="record-home"
        />

        <div className="slds-grid slds-wrap slds-m-top_medium slds-gutters">
            <div className="slds-col slds-col slds-size_2-of-3">
              <Tabs id="left" className="slds-var-p-around_small">
                <TabsPanel label="Details">
                  <form onSubmit={handleSubmit(onSubmit)}>
                    <Input iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: 'Edit',
                        }}
                        name="Edit"
                        category="utility"
                      />
                    } 
                    type="text" defaultValue={data.location.name} placeholder="Location name" name="name" label="Location name" ref={register} />
                    <Input iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: 'Edit',
                        }}
                        name="Edit"
                        category="utility"
                      />
                    } type="text" defaultValue={data.location.phone_number} placeholder="Phone number" name="phone_number" label="Phone number" ref={register} />
                    <Input iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: 'Edit',
                        }}
                        name="Edit"
                        category="utility"
                      />
                    } type="text" defaultValue={data.location.website} placeholder="Website" name="website" label="Website" ref={register} />

                    <Button onClick={handleSubmit(onSubmit)} type="submit">Update</Button>
                  </form>
                  
                </TabsPanel>
              <TabsPanel label="Contacts">
                <ContactsList location={data.location.id} />
              </TabsPanel>
              <TabsPanel label="Equipment">
                <EquipmentList location={data.location.id} equipment={equipment} />
              </TabsPanel>
              <TabsPanel label="Projects">
                <ProjectsList location={data.location.id} />
              </TabsPanel>
              </Tabs>
            </div>
            <div className="slds-col slds-col slds-size_1-of-3">
                <Tabs id="right" className="slds-var-p-around_small">
                  <TabsPanel label="Opportunities">
                    <OpportunitiesList company={data.company.id} location={data.location.id} />
                  </TabsPanel>
                </Tabs>
            </div>
        </div>
      </BrandBand>
      </IconSettings>
  )
}

const mapDispatchToProps = (dispatch) => {
  return {
    addEquipment: (array) => dispatch(addEquipment(array)),
  };
};

export default connect(null, mapDispatchToProps)(LocationDetail);
