import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter
 } 
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';



export function ProjectsList(props) {
	const SINGLE = gql`
	  query GetProjects {
	    getProjectsByLocation(location_id: ${props.location}) {
	      id
        created_at
        start_date
        kickoff_call
        technicians {
          id
        }
	    }
	  }
	`;
	const { loading, error, data } = useQuery(SINGLE);
	if (loading) return <p>Loading...</p>;
	if (error) return <p>Error :(</p>;

  const single = data.getProjectsByLocation;

	return (
		<IconSettings iconPath="/icons">
      <Card
        id="ProjectCard"
        headerActions={
         (
            <Button
              label="Add Project"
              onClick={console.log('Open modal')}
            />
          )
        }
        filter={
         (single.length > 0) && (
            <CardFilter onChange={() => {console.log('Change')}} />
          )
        }
        heading={`Projects (${single.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          single.length === 0 ? (
            <CardEmpty heading="No Projects">
              <Button label="Add Project" onClick={() => console.log('Clicked')} />
            </CardEmpty>
          ) : null
        }
      >
  			<DataTable
          fixedLayout
          items={single}
          id="getProject"
          on
        >
          <DataTableColumn
            label="Id"
            property="id"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Start date"
            property="start_date"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Kickoff call"
            property="kickoff_call"
          >

          </DataTableColumn>

        </DataTable>
      </Card>
		</IconSettings>
	)
}