import React from "react";
import ReactDOM from "react-dom";
import { ModalContext } from "./ModalContext";
import {Input,Modal,Button} from '@salesforce/design-system-react'; 
import {AddCompany} from './modals/AddCompany';
import {ModalAddContactToLocation} from './contacts/ModalAddContactToLocation'

const ModalContainer = () => {
  let { modalContent, modalHeading, handleModal, modal,form } = React.useContext(ModalContext);
  const closeModal = () => {
    handleModal(false)
  }
  if (modal) {
    return ReactDOM.createPortal(
      <Modal
      ariaHideApp={false}
      isOpen={modal}
      onRequestClose={closeModal}
      heading={modalHeading}
    >
    
      <section className="slds-p-around_large">

          {modalContent}
        </section>
      </Modal>,
      document.querySelector("#modal-root")
    );
  } else return null;
};

export default ModalContainer;
