import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter
 } 
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';

export function EquipmentList(props) {
	const SINGLE = gql`
	  query GetEquipment {
	    getEquipmentByLocation(location_id: ${props.location}) {
	      id
	      make 
        model
        created_at
        updated_at
        cfm
        data {
          id
        }
	    }
	  }
	`;

  const [equipment,setEquipment] = useState([]);
	const { loading, error, data } = useQuery(SINGLE);

  useEffect(() => {
    setEquipment(props.equipment)
  }, [props.equipment])

	if (loading) return <p>Loading...</p>;
	if (error) return <p>Error :(</p>;

  
  const single = data.getEquipmentByLocation;

	return (
		<IconSettings iconPath="/icons">
      <Card
        id="EquipmentCard"
        headerActions={
         (
            <Button
              label="Add Equipment"
              onClick={console.log('Open modal')}
            />
          )
        }
        filter={
         (equipment.length > 0) && (
            <CardFilter onChange={() => {console.log('Change')}} />
          )
        }
        heading={`Equipment (${equipment.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          equipment.length === 0 ? (
            <CardEmpty heading="No Equipment">
              <Button label="Add Equipment" onClick={() => console.log('Clicked')} />
            </CardEmpty>
          ) : null
        }
      >
  			<DataTable
          fixedLayout
          items={equipment}
          id="getEquipment"
          on
        >
          <DataTableColumn
            label="Building code"
            property="building_code"
          >

          </DataTableColumn>
          <DataTableColumn
            label="System name"
            property="system_name"
          >

          </DataTableColumn>

          <DataTableColumn
            label="CFM"
            property="cfm"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Water"
            property="water"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Power"
            property="power"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Size"
            property="size"
          >

          </DataTableColumn>

        </DataTable>
      </Card>
		</IconSettings>
	)
}