const items = [
  {
    id: 1,
    cfm: 30000,
    water: true,
    power: true,
    building_code: 'WHT',
    system_name: 'AHU-W1',
    building: 'Western Hem Trade Ctr'
  },
  {
    id: 2,
    cfm: 30000,
    water: true,
    power: true,
    building_code: 'WHT',
    system_name: 'AHU-W2',
    building: 'Western Hem Trade Ctr'
  },
  {
    id: 3,
    cfm: 13000,
    water: true,
    power: true,
    building_code: 'WHT',
    system_name: 'AHU-W3',
    building: 'Western Hem Trade Ctr'
  },
];

items.map(item => {
  if (item.cfm < 5000) {
    item.price = 250
    item.size = 'X-Small'
  }
  if (item.cfm >= 5000 && item.cfm <= 10000) {
    item.price = 500
    item.size = 'Small'
  } 
  if (item.cfm >= 10001 && item.cfm < 30000) {
    item.price = 1000
    item.size = 'Medium'
  }
  if (item.cfm >= 30000) {
    item.price = 1500
    item.size = 'Large'
  }
})

const Price = ({ children,item, ...props }) => (
  <DataTableCell title={children} {...props}>
    {item.cfm <= 10000 &&
      <span>$500</span>
    }
    {item.cfm >= 10001 && item.cfm < 30000 &&
      <span>$1000</span>
    }
    {item.cfm >= 30000 &&
      <span>$1500</span>
    }
  </DataTableCell>
);
Price.displayName = DataTableCell.displayName;

export function AddProposal(props) {

  const [selection, setSelection] = useState([]);
  const [step, setStep] = useState(1);
  const handleSelection = (e,data) => {
    setSelection(data.selection);
  }
  return (
    <IconSettings iconPath="/icons">
      {step === 1 &&
        <div>
          <DataTable
            fixedLayout
            items={items}
            id="getCompanies"
            selection={selection}
            onRowChange={handleSelection}
            selectRows="checkbox"
          >
            <DataTableColumn
              label="Building code"
              property="building_code" />
            <DataTableColumn
              label="System name"
              property="system_name" />
            <DataTableColumn
              label="Building"
              property="building" />
            <DataTableColumn
              label="CFM"
              property="cfm" />
            
          </DataTable>
         <Button onClick={() => setStep(2)}>Continue</Button>
        </div>
      }
      {step === 2 &&
        <div>
          <DataTable
            fixedLayout
            items={selection}
            id="selection"
          >
            <DataTableColumn
              label="Building code"
              property="building_code" />
            <DataTableColumn
              label="System name"
              property="system_name" />
            <DataTableColumn
              label="Building"
              property="building" />
            <DataTableColumn
              label="CFM"
              property="cfm" />
            <DataTableColumn
              label="Price"
              property="price">
            </DataTableColumn>
            
          </DataTable>

          <h2>Proposal total: {selection.reduce((prev,next) => prev + next.price,0)}</h2>

          <Button onClick={() => setStep(1)}>Back</Button>
          <Button variant="brand" onClick={() => setStep(2)}>Create proposal</Button>
        </div>
      }
  </IconSettings>
  )
}
