import React, { useState, useEffect } from "react";
import { useForm, Controller } from "react-hook-form";

import { Input, Modal, Button } from "@salesforce/design-system-react";

import { industries } from "../../utilities/industries-list";
import { states } from "../../utilities/states-list";

import { gql, useMutation } from "@apollo/client";

const ADD_COMPANY = gql`
  mutation CreateCompany($input: CompanyInput) {
    createCompany(input: $input) {
      id
      company_name
    }
  }
`;

export function AddCompany(props) {
  const [addCompany, { data, error }] = useMutation(ADD_COMPANY);

  const { register, handleSubmit, reset, watch, control } = useForm();

  const onSubmit = (data) => {
    addCompany({ variables: { input: data } });
  };

  return (
    <form onSubmit={handleSubmit(() => onSubmit(data))}>
      <Controller
        as={
          <label htmlFor="companyName">
            Company Name
            <Input name="company_name" type="text" ref={register} />
          </label>
        }
        control={control}
        name="company_name"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="website">
            Company Domain Name
            <Input name="website" type="text" ref={register} />
          </label>
        }
        control={control}
        name="website"
        defaultValue={""}
      />

      <Controller
        as={
          <select name="industry" ref={register}>
            {industries.map((industry) => {
              return (
                <option key={industry.name} value={industry.name}>
                  {industry.name}
                </option>
              );
            })}
          </select>
        }
        control={control}
        name="industry"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="phone_number">
            Phone Number
            <Input name="phone_number" type="text" ref={register} />
          </label>
        }
        control={control}
        name="phone_number"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="city">
            City
            <Input name="city" type="text" ref={register} />
          </label>
        }
        control={control}
        name="city"
        defaultValue={""}
      />

      <Controller
        as={
          <select name="state" ref={register}>
            {states.map((state) => {
              return (
                <option key={state.Code} value={state.State}>
                  {state.State}
                </option>
              );
            })}
          </select>
        }
        control={control}
        name="state"
        defaultValue={"Texas"}
      />

      <Input type="submit" />
    </form>
  );
}
