import React, { useState, useEffect } from "react";
import {
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter,
} from "@salesforce/design-system-react";

import { useQuery, gql } from '@apollo/client';
import { Link,useHistory,BrowserRouter as Router, Route, useLocation } from "react-router-dom";

export function OpportunitiesList(props) {
  const SINGLE = gql`
	  query GetLocations {
	    getOpportunitiesByLocation(location_id: ${props.location}) {
	      id
        amount
        stage
	    }
	  }
	`;
  const { loading, error, data } = useQuery(SINGLE);
  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
  return (
    <IconSettings iconPath="/icons">
      <Card
        id="OpportunityCard"
        headerActions={
          <Button
            label="Add Opportunity"
            onClick={console.log("i will Open modal")}
          />
        }
        filter={
          data.getOpportunitiesByLocation.length > 0 && (
            <CardFilter
              onChange={() => {
                console.log("Change");
              }}
            />
          )
        }
        heading={`Opportunities (${data.getOpportunitiesByLocation.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.getOpportunitiesByLocation.length === 0 ? (
            <CardEmpty heading="No Opportunities">
              <Button
                label="Add Opportunity"
                onClick={() => console.log("Clicked")}
              />
            </CardEmpty>
          ) : null
        }
      >
		<ul>
			{
        data.getOpportunitiesByLocation.map(opportunity => {
          return (
            <li><Link to={`/companies/${props.company}/opportunities/${opportunity.id}`}>{opportunity.name} {opportunity.amount} {opportunity.stage} </Link></li>
          )
        }) 
      }
		</ul>
    </Card>
  </IconSettings>
	)
}
