const companies = [
  { id: 1, name: "Company 1" },
  { id: 2, name: "Company 2" },
];

export function AddLocation(props) {
  const [inputValue, setInputValue] = useState("");
  const [selection, setSelection] = useState([]);

  const dispatch = useDispatch();

  const { register, handleSubmit, reset, watch, control } = useForm();

  const onSubmit = (data) => {
    console.log(data);
    dispatch(addLocation(data));
  };
  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Controller
        as={
          <label htmlFor="locationName">
            Name
            <Input name="name" type="text" ref={register} />
          </label>
        }
        control={control}
        name="name"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="address">
            Address
            <Input name="address" type="text" ref={register} />
          </label>
        }
        control={control}
        name="address"
        defaultValue={""}
      />

      <Controller
        as={
          <label htmlFor="city">
            City
            <Input name="city" type="text" ref={register} />
          </label>
        }
        control={control}
        name="city"
        defaultValue={""}
      />
      <br />
      <Controller
        as={
          <select name="state" ref={register}>
            {states.map((state) => {
              return (
                <option key={state.Code} value={state.State}>
                  {state.State}
                </option>
              );
            })}
          </select>
        }
        control={control}
        name="state"
        defaultValue={"Texas"}
      />
      <br />

      <Controller
        as={
          <label htmlFor="zip_code">
            ZIP Code
            <Input name="zip_code" type="text" ref={register} />
          </label>
        }
        control={control}
        name="zip_code"
        defaultValue={""}
      />

      <Input type="submit" value="Post Location" />
    </form>
  );
}
