import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter
 } 
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';



export function ContactsList(props) {
	const SINGLE = gql`
	  query GetContacts {
	    getContactsByLocation(location_id: ${props.location}) {
	      id
	      first_name
	      last_name
        email
	    }
	  }
	`;
	const { loading, error, data } = useQuery(SINGLE);
	if (loading) return <p>Loading...</p>;
	if (error) return <p>Error :(</p>;
	return (
		<IconSettings iconPath="/icons">
      <Card
        id="ContactCard"
        headerActions={
         (
            <Button
              label="Add Contacts"
              onClick={console.log('Open modal')}
            />
          )
        }
        filter={
         (data.getContactsByLocation.length > 0) && (
            <CardFilter onChange={() => {console.log('Change')}} />
          )
        }
        heading={`Contacts (${data.getContactsByLocation.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.getContactsByLocation.length === 0 ? (
            <CardEmpty heading="No Contacts">
              <Button label="Add Contact" onClick={() => console.log('Clicked')} />
            </CardEmpty>
          ) : null
        }
      >
  			<DataTable
          fixedLayout
          items={data.getContactsByLocation}
          id="getContacts"
          on
        >
          <DataTableColumn
            label="Id"
            property="id"
          >

          </DataTableColumn>
          <DataTableColumn
            label="First name"
            property="first_name"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Last name"
            property="last_name"
          >

          </DataTableColumn>
          <DataTableColumn
            label="Email"
            property="email"
          >

          </DataTableColumn>

        </DataTable>
      </Card>
		</IconSettings>
	)
}