import React, { useState, useEffect } from "react";
import { useForm, Controller } from "react-hook-form";

import { Input, Modal, Button } from "@salesforce/design-system-react";

import { industries } from "../../utilities/industries-list";
import { states } from "../../utilities/states-list";

import { gql, useMutation } from "@apollo/client";

const ADD_COMPANY = gql`
  mutation CreateCompany($input: CompanyInput!) {
    createCompany(input: $input) {
      company_name
      state
      city
      category
      created_by {
        id
      }
    }
  }
`;

export function CompanyForm({ auth }) {
  //redux user
  const { id } = auth.user;

  const [addCompany, { error }] = useMutation(ADD_COMPANY);
  console.log(error);
  const { register, handleSubmit, reset, watch, control } = useForm();

  const onSubmit = (data) => {
    data["created_by"] = id;

    console.log(data);

    addCompany({ variables: { input: { ...data, website: "test" } } });
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Controller
        as={
          <label htmlFor="companyName">
            Company Name
            <Input name="company_name" type="text" ref={register} />
          </label>
        }
        control={control}
        name="company_name"
        defaultValue={""}
      />
      <br />

      <Controller
        as={
          <select name="state" ref={register}>
            {states.map((state) => {
              return (
                <option key={state.Code} value={state.State}>
                  {state.State}
                </option>
              );
            })}
          </select>
        }
        control={control}
        name="state"
        defaultValue={"Texas"}
      />
      <br />

      <Controller
        as={
          <label htmlFor="city">
            City
            <Input name="city" type="text" ref={register} />
          </label>
        }
        control={control}
        name="city"
        defaultValue={""}
      />
      <br />
      <br />

      <Controller
        as={
          <select name="category" ref={register}>
            {industries.map((industry) => {
              return (
                <option key={industry.name} value={industry.name}>
                  {industry.name}
                </option>
              );
            })}
          </select>
        }
        control={control}
        name="category"
        defaultValue={""}
      />

      {/* 

      <Controller
        as={
          <label htmlFor="website">
            Company Domain Name
            <Input name="website" type="text" ref={register} />
          </label>
        }
        control={control}
        name="website"
        defaultValue={""}
      />
      
      
      <Controller
        as={
          <label htmlFor="phone_number">
            Phone Number
            <Input name="phone_number" type="text" ref={register} />
          </label>
        }
        control={control}
        name="phone_number"
        defaultValue={""}
      /> */}

      <Input type="submit" />
    </form>
  );
}
