import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  IconSettings,
  CardFilter
 }
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';

export function ProjectsList(props) {
	const SINGLE = gql`
    query GetCompanyByID($id: ID!) {
      company(id: $id) {
        locations {
          projects {
            project_name
          }
        }
      }
    }
  `;
	const { loading, error, data } = useQuery(SINGLE, {
    variables: {id: Number(props.company)}
  });
	if (loading) return <p>Loading...</p>;
  	if (error) return <p>Error :(</p>;
	return (
    <IconSettings iconPath="/icons">
      <Card
        id="ProjectsCard"
        headerActions={
         (
            <Button
              label="Add Project"
              onClick={console.log('Open modal')}
            />
          )
        }
        filter={
         (data.company.locations.length > 0) && (
            <CardFilter onChange={() => {console.log('Change')}} />
          )
        }
        heading={`Projects (${data.company.locations.length})`}
        icon={<Icon category="standard" name="contact" size="small" />}
        empty={
          data.company.locations.length === 0 ? (
            <CardEmpty heading="No Projects">
              <Button label="Add Project" onClick={() => console.log('Clicked')} />
            </CardEmpty>
          ) : null
        }
      >
  		<DataTable
            fixedLayout
            items={data.locations.projects}
            id="getContacts"
            on
          >
            <DataTableColumn
              label="Id"
              property="id"
            >

            </DataTableColumn>
          </DataTable>
    </Card>
  </IconSettings>
	)
}