import React, { useState, useEffect } from "react";
import { useQuery, gql, useMutation } from "@apollo/client";
import { useForm } from "react-hook-form";
import {
  PageHeader,
  PageHeaderControl,
  IconSettings,
  Icon,
  Tabs,
  TabsPanel,
  ButtonStateful,
  Button,
  ButtonGroup,
  Dropdown,
  BrandBand,
  Input,
  InputIcon,
} from "@salesforce/design-system-react"; 
import {
  Link,
  useHistory,
  BrowserRouter as Router,
  Route,
  useLocation,
} from "react-router-dom";

import { ContactsList } from "./ContactsList";
import { LocationsList } from "./LocationsList";
import OpportunitiesList from "./OpportunitiesList";
import { ProjectsList } from "./ProjectsList";

export function CompanyDetail({ match, props }) {
  const id = Number(match.params.companyId);
  const SINGLE = gql`
    query GetCompanyByID($id: ID!) {
      company(id: $id) {
        id
        company_name
        state
        city
        created_at
        locations {
          id
        }
      }
    }
  `;
  const UPDATE_COMPANY = gql`
      mutation UpdateCompany($id: ID!, $input: CompanyUpdate) {
        updateCompany(where:{id:$id},data:$input){
          id
        }
      }
    `

  const { loading, error, data } = useQuery(SINGLE, {
    variables: { id: parseInt(id) },
  });
  const [updateCompany, { companyData }] = useMutation(UPDATE_COMPANY);

  const [modalOpen, toggleModalOpen] = useState(false);

  const { register, handleSubmit, errors } = useForm();
  const onSubmit = (data,id) => {
    console.log('id',match.params.companyId);
    updateCompany({variables: {id:match.params.companyId, input: data}})
  };
  console.log(errors);

  const actions = () => (
    <React.Fragment>
      <PageHeaderControl>
        <ButtonGroup variant="list" id="button-group-page-header-actions">
          <Button label="New Contact" />
          <Button label="New Location" />
          <Button label="New Opportunity" />
          <Dropdown
            align="right"
            assistiveText={{ icon: "More Options" }}
            iconCategory="utility"
            iconName="down"
            iconVariant="border-filled"
            id="dropdown-record-home-example"
            options={[
              { label: "Menu Item One", value: "A0" },
              { label: "Menu Item Two", value: "B0" },
              { label: "Menu Item Three", value: "C0" },
              { type: "divider" },
              { label: "Menu Item Four", value: "D0" },
            ]}
          />
        </ButtonGroup>
      </PageHeaderControl>
    </React.Fragment>
  );

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;
  const details = [
    {
      label: "Website",
      content: data.company.website,
      truncate: true,
    },
    { label: "Created at", content: data.company.created_at },
    {
      label: "Field 3",
      content: <a href="javascript:void(0);">Hyperlink</a>,
    },
    {
      label: "Field 4",
      content: "Description (2-line truncatencation)",
      truncate: true,
    },
  ];

  return (
    <IconSettings iconPath="/icons">
      <BrandBand
        id="brand-band-lightning-blue"
        className="slds-p-around_small"
        theme="lightning-blue"
      >
        <PageHeader
          details={details}
          icon={
            <Icon
              assistiveText={{ label: "User" }}
              category="standard"
              name="account"
            />
          }
          label="Company"
          onRenderActions={actions}
          title={data.company.company_name}
          variant="record-home"
        />

        <div className="slds-grid slds-wrap slds-m-top_medium slds-gutters">
          <div className="slds-col slds-col slds-size_2-of-3">
            <Tabs id="left" className="slds-var-p-around_small">
              <TabsPanel label="Details">
                <form onSubmit={handleSubmit(onSubmit)}>
                  <Input
                    iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: "Edit",
                        }}
                        name="Edit"
                        category="utility"
                      />
                    }
                    type="text"
                    defaultValue={data.company.company_name}
                    placeholder="Company name"
                    name="company_name"
                    label="Company name"
                    ref={register}
                  />
                  <Input
                    iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: "Edit",
                        }}
                        name="Edit"
                        category="utility"
                      />
                    }
                    type="text"
                    defaultValue={data.company.city}
                    placeholder="City"
                    name="city"
                    label="City"
                    ref={register}
                  />
                  <Input
                    iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: "Edit",
                        }}
                        name="Edit"
                        category="utility"
                      />
                    }
                    type="text"
                    defaultValue={data.company.state}
                    placeholder="State"
                    name="state"
                    label="State"
                    ref={register}
                  />

                  <Button onClick={handleSubmit(onSubmit)} type="submit">
                    Update
                  </Button>
                </form>
              </TabsPanel>
              <TabsPanel label="Contacts">
                <ContactsList company={data.company.id} />
              </TabsPanel>
              <TabsPanel label="Locations">
                <LocationsList company={data.company.id} />
              </TabsPanel>
              <TabsPanel label="Projects">
                <ProjectsList company={data.company.id} />
              </TabsPanel>
            </Tabs>
          </div>
          <div className="slds-col slds-col slds-size_1-of-3">
            <Tabs id="right" className="slds-var-p-around_small">
              <TabsPanel label="Opportunities">
                <OpportunitiesList company={data.company.id} />
              </TabsPanel>
            </Tabs>
          </div>
        </div>
      </BrandBand>
    </IconSettings>
  );
}
