import React, { useState, useEffect } from "react";
import { useQuery, gql } from "@apollo/client";
import { useForm } from "react-hook-form";
import {
  PageHeader,
  PageHeaderControl,
  IconSettings,
  Icon,
  Tabs,
  TabsPanel,
  ButtonStateful,
  Button,
  ButtonGroup,
  Dropdown,
  BrandBand,
  Input,
  InputIcon,
} from "@salesforce/design-system-react";

import {
  Link,
  useHistory,
  BrowserRouter as Router,
  Route,
  useLocation,
} from "react-router-dom";

import {ActivityTab} from './ActivityTab';

export function ContactDetail({ match, props }) {
  const SINGLE = gql`
    query contactByID($id: ID!) {
      contact(id: $id) {
        id
        contact_name
        company_title
        created_at
        company {
          id
          company_name
        }
      }
    }
  `;

  const { loading, error, data } = useQuery(SINGLE, {
    variables: {id: match.params.contactId}
  });

  const [modalOpen, toggleModalOpen] = useState(false);

  const { register, handleSubmit, errors } = useForm();
  const onSubmit = (data) => console.log(data);

  const actions = () => (
    <React.Fragment>
      <PageHeaderControl>
        <ButtonGroup variant="list" id="button-group-page-header-actions">
          <Button label="Assign to Location" />
          <Dropdown
            align="right"
            assistiveText={{ icon: "More Options" }}
            iconCategory="utility"
            iconName="down"
            iconVariant="border-filled"
            id="dropdown-record-home-example"
            options={[
              { label: "Menu Item One", value: "A0" },
              { label: "Menu Item Two", value: "B0" },
              { label: "Menu Item Three", value: "C0" },
              { type: "divider" },
              { label: "Menu Item Four", value: "D0" },
            ]}
          />
        </ButtonGroup>
      </PageHeaderControl>
    </React.Fragment>
  );

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  const details = [
    {
      label: "Company",
      content: <Link to={`/companies/1544`}>Bridge</Link>,
      truncate: true,
    },
    { label: "Created at", content: data.contact.created_at },
    {
      label: "Field 3",
      content: <a href="javascript:void(0);">Hyperlink</a>,
    },
    {
      label: "Field 4",
      content: "Description (2-line truncatencation)",
      truncate: true,
    },
  ];

  return (
    <IconSettings iconPath="/icons">
      <BrandBand
        id="brand-band-lightning-blue"
        className="slds-p-around_small"
        theme="lightning-blue"
      >
        <PageHeader
          details={details}
          icon={
            <Icon
              assistiveText={{ label: "User" }}
              category="standard"
              name="account"
            />
          }
          label="Company"
          onRenderActions={actions}
          title={data.contact.contact_name}
          variant="record-home"
        />

        <div className="slds-grid slds-wrap slds-m-top_medium slds-gutters">
          <div className="slds-col slds-col slds-size_2-of-3">
            <Tabs id="left" className="slds-var-p-around_small">
              <TabsPanel label="Details">
                <form onSubmit={handleSubmit(onSubmit)}>
                  <Input
                    iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: "Edit",
                        }}
                        name="Edit"
                        category="utility"
                      />
                    }
                    type="text"
                    defaultValue={data.contact.contact_name}
                    placeholder="Contact name"
                    name="contact_name"
                    label="Contact name"
                    ref={register}
                  />
                  <Input
                    iconRight={
                      <InputIcon
                        assistiveText={{
                          icon: "Edit",
                        }}
                        name="Edit"
                        category="utility"
                      />
                    }
                    type="text"
                    defaultValue={data.contact.title}
                    placeholder="Title"
                    name="company_title"
                    label="Title"
                    ref={register}
                  />

                  <Button onClick={handleSubmit(onSubmit)} type="submit">
                    Update
                  </Button>
                </form>
              </TabsPanel>
              <TabsPanel label="Locations">

              </TabsPanel>
              <TabsPanel label="Opportunities">

              </TabsPanel>
              <TabsPanel label="Proposals">

              </TabsPanel>
              <TabsPanel label="Projects">

              </TabsPanel>
            </Tabs>
          </div>
          <div className="slds-col slds-col slds-size_1-of-3">
            <Tabs id="right" className="slds-var-p-around_small">
              <TabsPanel label="Activity">
                <ActivityTab />
              </TabsPanel>
            </Tabs>
          </div>
        </div>
      </BrandBand>
    </IconSettings>
  );
}
