import React, {useState,useEffect} from 'react';
import {
 DataTable,
  DataTableColumn,
  DataTableCell,
  BrandBand,
  PageHeaderControl,
  PageHeader,
  Button,
  ButtonGroup,
  Dropdown,
  Icon,
  IconSettings,
  Modal} 
from '@salesforce/design-system-react'; 
import { useQuery, gql } from '@apollo/client';

import { ModalContext } from "../ModalContext";
import {AddContactForm} from './ModalAddContact';
import { Link,useHistory,BrowserRouter as Router, Route, useLocation } from "react-router-dom";

const RecordLink = ({children, item, ...props}) => (
  <DataTableCell title={children} {...props}>
    <Link to={`/contacts/${item.id}`}>
      {children}
    </Link>
  </DataTableCell>
)
RecordLink.displayName = DataTableCell.displayName;

const CONTACTS = gql`
  {
    contacts {
      id
      contact_name
      company_title
    }
  }
`;

export function ContactsList(props) {
  const { loading, error, data } = useQuery(CONTACTS);
  const { handleModal } = React.useContext(ModalContext);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error :(</p>;

  function toggleModal() {
    handleModal(<AddContactForm />,'Add Contact')
  }

  const actions = () => (
    <PageHeaderControl>
      <ButtonGroup variant="list" id="button-group-page-header-actions">
        <ButtonGroup variant="list" id="button-group-page-header-actions">
          <Button variant="brand" label="Add Contact" onClick={toggleModal}/>
        </ButtonGroup>
      </ButtonGroup>
    </PageHeaderControl>
  );


  return (
    <IconSettings iconPath="/icons">
      <BrandBand
          id="brand-band-lightning-blue"
          className="slds-p-around_small"
          theme="lightning-blue"
        >
      <PageHeader
        icon={
          <Icon
            assistiveText={{ label: 'User' }}
            category="standard"
            name="opportunity"
          />
        }
        label="CONTACTS"
        title={`Your contacts`}
        variant="record-home"
        onRenderActions={actions}
      />
      <DataTable
            fixedLayout
            items={data.contacts}
            id="getContacts"
            selectRows="checkbox"
          >
            <DataTableColumn
              label="Id"
              property="id"
            >

            </DataTableColumn>
            <DataTableColumn
              label="Contact name"
              property="contact_name"
            >
              <RecordLink />

            </DataTableColumn>
            <DataTableColumn
              label="Title"
              property="company_title"
            >

            </DataTableColumn>
        </DataTable>
      </BrandBand>
  </IconSettings>
  )
}