import React, {useState} from 'react';
import {
  DataTable,
  DataTableColumn,
  DataTableCell,
  ButtonGroup,
  Button,
  Card,
  CardEmpty,
  Icon,
  DataTableRowActions,
  Dropdown,
  IconSettings,
  CardFilter,
} from "@salesforce/design-system-react";

export function ContactsTab(props) {
	const [contacts, setContacts] = useState(props.contacts);
	return (
		<Card
	        id="ContactCard"
	        headerActions={
	          <Button label="Add Contacts"/>
	        }
	        filter={
	          contacts.length > 0 && (
	            <CardFilter
	              onChange={() => {
	                console.log("Change");
	              }}
	            />
	          )
	        }
	        heading={`Contacts (${contacts.length})`}
	        icon={<Icon category="standard" name="contact" size="small" />}
	        empty={
	          contacts.length === 0 ? (
	            <CardEmpty heading="No Contacts">
	              <Button
	                label="Add Contacts"
	              />
	            </CardEmpty>
	          ) : null
	        }
	      >
	        <DataTable
	          fixedLayout
	          items={contacts}
	          id="contacts"
	          on
	        >
	          <DataTableColumn label="Id" property="id"></DataTableColumn>

	          <DataTableColumn label="First name" property="first_name">

	          </DataTableColumn>
	          <DataTableColumn label="Last name" property="last_name">
	          
	          </DataTableColumn>
	          <DataTableColumn label="Email" property="email">
	          
	          </DataTableColumn>
	          <DataTableColumn label="Role" property="role">
	          
	          </DataTableColumn>
	          <DataTableRowActions
				options={[
					{
						id: 0,
						label: 'Send Proposal',
						value: '1',
					},
					{
						id: 1,
						label: 'Email',
						value: '2',
					},
					{
						id: 2,
						label: 'Remove',
						value: '3',
					}				]}
				dropdown={<Dropdown length="7" />}
				/>
	        </DataTable>
	      </Card>
	)
}